import React, { createContext, useContext, useState, useEffect } from 'react';
import axios from 'axios';

const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [currentUser, setCurrentUser] = useState(null);
  const [isLoginModalOpen, setIsLoginModalOpen] = useState(false);

  useEffect(() => {
    const checkLoginStatus = async () => {
      const token = localStorage.getItem('token');
      if (!token) {
        console.error('No token found');
        setIsLoginModalOpen(true); // Show login modal if no token is found
        return;
      }

      try {
        const response = await axios.get('/api/check-login', {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        });
        if (response.data.loggedIn) {
          setCurrentUser(response.data.user); // Ensure 'user' contains username and other details
        } else {
          localStorage.removeItem('token');
          setIsLoginModalOpen(true); // Show login modal if login check fails
        }
      } catch (error) {
        console.error("Failed to check login status:", error);
        localStorage.removeItem('token');
        setIsLoginModalOpen(true); // Show login modal on error
      }
    };

    checkLoginStatus();
  }, []);

  const logout = async () => {
    try {
      const response = await axios.post('/api/logout');
      if (response.data.message === 'Logout successful') {
        localStorage.removeItem('token');
        setCurrentUser(null);
        alert('Logged out successfully.');
        setIsLoginModalOpen(true); // Show login modal after logout
      } else {
        alert('Error during logout.');
      }
    } catch (error) {
      console.error('Error during logout:', error);
      alert('Error during logout: ' + error.message);
    }
  };

  return (
    <AuthContext.Provider value={{ currentUser, setCurrentUser, logout, isLoginModalOpen, setIsLoginModalOpen }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => useContext(AuthContext);